.frontflex {
	display: flex;
	height: 100%;
	width: 100%;
	justify-content: center;
	gap: 2em;
}

.frontflex > div {
	display: flex;
	justify-content: center;
	align-items: center;
}

.front {
}


.navs {
	font-size: min(2em, 2vw);
	display:flex;
	flex-direction: column-reverse;
	align-items: center;
	transform: skewY(-15deg);
	margin-left: 2.5em;
	margin-top: 5em;
}


.navs button {
	width: 15em;
	border: none;
	padding: 0.5em 1em;
	position: relative;
	transition: 0.5s;
	border: solid 1px black;
}

.navs button:hover {
	transform: translateX(-2.5em);
}


.navs button:active {
	transition: 2s;
	transform: skewY(15deg);
}

.navs button:focus {
	transform: scale(50) skewY(15deg);
	opacity: 0;
}

.navs button:before {
	cursor: pointer;
	content: "";
	width: 2.5em;
	height: 100%;
	background-color: var(--bs-gray);
	position: absolute;
	left: -2.5em;
	top: 0;
	transform-origin: right;
	transform: skewY(45deg);
	border: solid 1px black;
}

.navs button:after {
	cursor: pointer;
	content: "";
	width: 100%;
	height: 2.5em;
	background-color: var(--bs-gray-500);
	position: absolute;
	left: 0;
	top: -2.5em;
	transform-origin: bottom;
	transform: skewX(45deg);
	border: solid 1px black;
	border-top-right-radius: 1em;
}


@media only screen and (max-width: 600px) {
  .frontflex {
	gap: 4em;
  	text-align: center;
    flex-direction: column;
  }

  .navs {
  	font-size: 1em;
	margin-top: 0em;
  }
}
